/**
 * Base SCSS
 */
@font-face {
	font-family: Avenir_Book;
	src: url("/wp-content/themes/verify/assets/css/font/AvenirLTStd-Book.otf");
}
@font-face {
	font-family: Avenir_Book;
	src: url("/clients/verify/wp-content/themes/verify/assets/css/font/AvenirLTStd-Book.otf");
}
@font-face {
	font-family: Avenir_Med;
	src: url("/wp-content/themes/verify/assets/css/font/AvenirMedium.ttf");
}
@font-face {
	font-family: Avenir_Med;
	src: url("/clients/verify/wp-content/themes/verify/assets/css/font/AvenirMedium.ttf");
}
@font-face {
	font-family: Avenir_Rom;
	src: url("/wp-content/themes/verify/assets/css/font/AvenirLTStd-Roman.otf");
}
@font-face {
	font-family: Avenir_Rom;
	src: url("/clients/verify/wp-content/themes/verify/assets/css/font/AvenirLTStd-Roman.otf");
}
@font-face {
	font-family: Avenir_Hea;
	src: url("/wp-content/themes/verify/assets/css/font/AvenirHeavy.ttf");
}
@font-face {
	font-family: Avenir_Hea;
	src: url("/clients/verify/wp-content/themes/verify/assets/css/font/AvenirHeavy.ttf");
}
* {
	font-family: Avenir;
}

.wrap {
	display: table;
	width: 100%;
	height: 100%;
	font-size: 5vh;
	padding: 3vh;
	background: $color--primary;
	color: $w;
}

.content {
	display: table-cell;
	width: 100%;
	vertical-align: middle;
	margin: 0 auto;
	text-align: center;

	h1,
	p {
		font-family: Lato;
		font-weight: 100;
		@include r(240) {
			font-weight: 100;
		}
		@include r(320) {
			font-weight: 100;
		}
		@include r(480) {
			font-weight: 100;
		}
		@include r(767) {
			font-weight: 100;
		}
		@include r(1024) {
			font-weight: 100;
		}
		@include r(1280) {
			font-weight: 100;
		}
	}

	h1 {
		text-transform: uppercase;
		@include r(240) {
			text-transform: uppercase;
		}
		@include r(320) {
			text-transform: uppercase;
		}
		@include r(480) {
			text-transform: uppercase;
		}
		@include r(767) {
			text-transform: uppercase;
		}
		@include r(1024) {
			text-transform: uppercase;
		}
		@include r(1280) {
			text-transform: uppercase;
		}
	}
}
@keyframes b {
	0%,
	to {
		transform-origin: center;
		transform: scale(0.8);
	}
	50% {
		transform: scale(1.1);
	}
}
@keyframes pulsate {
	0% {
		-webkit-transform: scale(0.1, 0.1);
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	100% {
		-webkit-transform: scale(1.2, 1.2);
		opacity: 0;
	}
}

// MASTHEAD -------------------------------------------------------------------------------------------------------------
#masthead {
	padding: 6.5rem 12% 3rem;
	position: relative;
	height: 720px;
	color: aliceblue;
	display: flex;
	align-items: center;
	font-size: 1rem;
	text-align: left;
	.title {
		color: #ffffff;
		font-family: Avenir_Med;
		font-size: 60px;
		font-weight: 400;
		line-height: 1.2;
		text-align: left;
		padding-bottom: 0.3rem;
		// padding-top: 10%;
	}
	.text .text {
		padding-top: 2%;
		// padding-bottom: 20%;
		line-height: 1.6;
		width: 100%;
		max-width: 600px;
		font-size: 18px;
		margin-bottom: 8%;
		font-family: Avenir_Book;
	}
	.covid_button {
		display: block;
		.white_arrow {
			position: relative;
			object-fit: contain;
		}

		.ring-container {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			height: 25px;
			width: 25px;
			padding: 0;
			margin: 0;
		}
		.circle {
			width: 15px;
			height: 15px;
			background-color: white;
			border-radius: 50%;
			position: relative;
		}
		.button_text {
			display: inline-block;
			// margin-top: 0.5rem;
		}
		.ringring {
			border: 3px solid white;
			-webkit-border-radius: 30px;
			height: 25px;
			width: 25px;
			// position: absolute;
			position: absolute;
			-webkit-animation: pulsate 2s ease-out;
			-webkit-animation-iteration-count: infinite;
			opacity: 0;
		}
	}
	@media screen and (max-width: 991px) {
		padding: 2rem 10% 0rem;
		display: flex;
		height: 600px;
		.title {
			color: #ffffff;
			font-family: Avenir;
			text-align: left;
			font-size: 50px;
			font-weight: 400;
			line-height: 1.2;
			padding-bottom: 1rem;
		}
		.text .text {
			padding-bottom: 1rem;
			width: auto;
			// top: 15px;
		}
		.covid_button {
			// margin-top: 5%;
			.button_text {
				font-size: 16px;
			}
		}
	}
	@media screen and (max-width: 767px) {
		padding: 120px 7% 80px;
		display: flex;
		height: 80vh;
		.title {
			color: #ffffff;
			font-family: Avenir;
			text-align: left;
			font-size: 2.3rem;
			font-weight: 500;
			line-height: 1.2;
			padding-bottom: 2%;
			width: 120%;
			max-width: 450px;
		}
		.text .text {
			padding-bottom: 0;
			width: auto;
			font-size: 1rem;
			line-height: 1.4;
			font-weight: 100;
			padding-top: 12%;
			margin-bottom: 20%;
		}
		.covid_button {
			.button_text {
				font-size: 0.9rem;
			}
		}
	}
}

