// VALUES -------------------------------------------------------------------------------------------------------------
#values {
	padding: 1rem 12% 5%;
	background-color: #f8fbfd;
	position: relative;
	.dark_background {
		background: url("assets/img/values_background.svg");
		background-repeat: no-repeat;
		position: absolute;
		background-size: cover;
		width: 100%;
		height: 60%;
		bottom: 0;
		z-index: 1;
		left: 0;
		right: 0;
	}
	.card {
		position: relative;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0);
		padding: 5rem;
		text-align: left;
		background-image: url("assets/img/val_card_background.svg");
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		border: none;
		border-radius: 10px;
		-webkit-border-radius: 10px;
		-moz-border-radius: 10px;
		height: auto;
		width: 100%;
		z-index: 2;
		.title {
			color: #ffffff;
			font-family: Avenir_Med;
			font-size: 45px;
			font-weight: 100;
			letter-spacing: 0;
			line-height: 50px;
		}
		.subtitle {
			color: #ffffff;
			font-family: Avenir;
			font-size: 14px;
			font-weight: 500;
			letter-spacing: 3px;
			line-height: 1.2;
			text-transform: uppercase;
			padding-bottom: 1rem;
		}
		.icon {
			img {
				width: 20%;
				padding-bottom: 2rem;
				padding-top: 2rem;
			}
		}
		.values-content {
			padding-top: 5%;
		}
		.value-title {
			height: 48px;
			width: 250px;
			color: #ffffff;
			font-family: Avenir;
			font-size: 22px;
			font-weight: 500;
			letter-spacing: 0;
			line-height: 24px;
		}

		.value-text {
			color: #ffffff;
			font-family: Avenir;
			font-size: 1rem;
			font-weight: 300;
			letter-spacing: 0;
			line-height: 1.4;
			margin-top: 0.8rem;
			text-align: left;
			margin-right: 2.5rem;
		}
	}
	.toggle {
		color: $gb;
		float: left;
	}
	.value_quote {
		text-align: center;
		margin-top: 5%;
		z-index: 2;
		border: none;
		overflow: visible;
		border-raidus: none;
		.quote {
			color: #ffffff;
			font-family: Avenir;
			font-size: 40px;
			font-weight: 500;
			letter-spacing: 0;
			line-height: 40px;
		}
		.exp {
			color: #ffffff;
			font-family: Avenir;
			font-size: 20px;
			font-weight: 300;
			letter-spacing: 0;
			line-height: 31px;
			padding: 1.2rem 10% 1.2rem;
		}
		.author {
			color: $g;
			font-family: Avenir;
			font-size: 16px;
			font-weight: 500;
			letter-spacing: 3.43px;
			line-height: 22px;
			text-align: center;
			text-transform: uppercase;
		}
	}
	@media screen and (max-width: 1199px) {
		overflow-x: hidden;
		padding: 4% 0% 5%;
