					width: 280px;
					color: #ffffff;
					font-family: Avenir;
					font-size: 1.2rem;
					font-weight: 700;
					letter-spacing: 0;
					line-height: 1.2;
					margin-bottom: 0.9rem;
				}

				.value-text {
					color: #ffffff;
					font-family: Avenir;
					font-size: 0.85rem;
					font-weight: 100;
					letter-spacing: 0;
					line-height: 1.6;
					margin-top: 0.1rem;
					text-align: left;
					margin-right: 0.1rem;
				}
			}
		}
		.value_quote {
			text-align: center;
			margin-top: 7%;
			.quote {
				color: #ffffff;
				font-family: Avenir;
				font-size: 1.5rem;
				font-weight: 500;
				letter-spacing: 0;
				line-height: 1.6;
				width: 100%;
				max-width: 200px;
				text-align: center;
				margin: 0 auto;
			}
			.exp {
				color: #ffffff;
				font-family: Avenir;
				font-size: 16px;
				font-weight: 500;
				// letter-spacing: 3.43px;
				line-height: 1.2;
				text-align: center;
				padding: 1rem 10%;
			}
			.author {
				color: $g;
				font-family: Avenir;
				font-size: 16px;
				font-weight: 500;
				letter-spacing: 3.43px;
				line-height: 1.6;
				text-align: center;
			}
		}
	}
}

