	.progress-bar {
		width: 1px;
		background: #26ac77;
		height: 0%;
		z-index: 2;
		max-height: 100%;
		transition: 600ms all ease;
	}
	.progress-bar:after {
		content: "";
		border-radius: 10px;
		background: #000;
		margin-top: -3px;
		z-index: 2;
		max-height: 100%;
		transition: 600ms all ease;
	}
	.image {
		position: relative;
		img {
			width: 80%;
			height: auto;
			object-fit: cover;
			border-radius: 8px;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-left: 10%;
		}
	}
	.row.right {
		flex-direction: row-reverse;
		.image .dots {
			left: unset;
			right: -40%;
		}
	}

	.icon {
		img {
			width: 8%;
			padding-bottom: 1rem;
		}
		.PP_icon {
			-webkit-filter: grayscale(1); /* Google Chrome, Safari 6+ & Opera 15+ */
			filter: grayscale(1);
			transition: 0.5s all ease;
		}
		.active_icon {
			-webkit-filter: invert(32%) sepia(9%) saturate(613%) hue-rotate(174deg)
				brightness(95%) contrast(87%);
			filter: invert(32%) sepia(9%) saturate(613%) hue-rotate(174deg)
				brightness(95%) contrast(87%);
			transition: 0.5s all ease;
		}
	}
	.title {
		color: #161616;
		font-family: Avenir;
		font-size: 24px;
		font-weight: 550;
		letter-spacing: 0;
		line-height: 26px;
		padding-bottom: 6%;
	}
	.sub {
		color: #161616;
		font-family: Avenir;
		font-size: 16px;
		font-weight: 300;
		letter-spacing: 0;
		line-height: 24px;
		padding-bottom: 30%;
	}
	.text_proc {
		// padding: 5rem 8rem 5rem;
		width: 100%;
		margin: auto auto;
		position: relative;
		padding-top: 13%;
		max-width: 350px;
		@media screen and (min-width: 1920px) {
			padding-top: 12%;
		}
	}
	.number {
		position: absolute;
		font-size: 23rem;
		z-index: 2;
		top: -35%;
		bottom: 50%;
		left: 2.5rem;
		margin: auto;
		opacity: 0.04;
		font-family: Avenir Heavy;
	}
	.num_mobile {
		display: none;
	}
	.row_pp {
		padding-bottom: 10%;
	}
	.pp_sub {
		color: #505760;
		font-family: Avenir;
		font-size: 14px;
		font-weight: 500;
		letter-spacing: 3px;
		line-height: 50px;
		text-align: center;
		padding-top: 2%;
		text-transform: uppercase;
		// height: 120px;
	}
	.pp_title {
		height: 100px;
		color: #282b2f;
		font-family: Avenir_Med;
		font-size: 45px;
		font-weight: 300;
		letter-spacing: 0;
		line-height: 34px;
		text-align: center;
		margin-bottom: 2%;
	}
	@media screen and (max-width: 991px) {
