				border-bottom: 2px solid $w;
				outline: none;
				width: 100%;
				overflow: hidden;
			}
			.row {
				padding-bottom: 1rem;
			}
			#f-name,
			#email {
				margin-bottom: 5%;
			}
			.wpcf7-submit {
				top: -40%;
			}
			.wpcf7-form {
				padding-top: 1.5rem;
			}
			.wpcf7-response-output {
				border: none;
				color: $r;
				transform: translateY(-160px);
				font-size: 0.8rem;
				text-transform: none;
				padding: 1rem;
			}
		}
	}
